"use client";
import React from "react";
import SignInForm from "../components/auth/signInFrom";
import useFormData from "../hooks/UseFormData";
import Alert from "../components/auth/Alert";
import SignUpForm from "../components/auth/signUpFrom";
import { useAuth } from "../context/AuthContext";

export default function AuthForms() {
  const [activeTab, setActiveTab] = React.useState("signin");

  const { loading, login, successMessage, errors  , signUp} = useAuth();

  const SignInFormValue = useFormData({
    email: "",
    password: "",
    rememberMe: false,
  });

  

  const SignUpFromValue = useFormData({
    fullName: "",
    username: "",
    email: "",
    password: "",
    Terms: false,
  });

  const handleSignUp = async(e: { preventDefault: () => void} )=>{
    e.preventDefault();
    try{
    await  signUp(SignUpFromValue.formData);
      setActiveTab("signin")
    }catch (error) {
      throw error;
    }
  }

  const handleSignIn = async(e: { preventDefault: () => void }) => {
    e.preventDefault();
    try {
     await login(SignInFormValue.formData);
    } catch (error) {
      throw error;
    }
  };

  React.useEffect(() => {
    if (activeTab == "signin") {
      SignUpFromValue.resetForm();
    } else {
      SignInFormValue.resetForm();
    }
  }, [activeTab]);

  return (
    <div className="min-h-screen bg-linear-to-br from-slate-900 via-blue-900 to-slate-900 flex items-center justify-center p-4">
      <div className="w-full max-w-md bg-white rounded-2xl shadow-2xl overflow-hidden">
        {/* Tabs */}
        <div className="flex border-b border-gray-200">
          <button
            onClick={() => setActiveTab("signin")}
            className={`flex-1 py-4 text-center font-semibold transition-all ${
              activeTab === "signin"
                ? "bg-blue-600 text-white"
                : "bg-white text-gray-600 hover:text-gray-900"
            }`}
          >
            Sign In
          </button>
          <button
            onClick={() => setActiveTab("signup")}
            className={`flex-1 py-4 text-center font-semibold transition-all ${
              activeTab === "signup"
                ? "bg-blue-600 text-white"
                : "bg-white text-gray-600 hover:text-gray-900"
            }`}
          >
            Sign Up
          </button>
        </div>

        {/* Form Content */}
        <div className="p-8">
          {((errors as any)?.general || successMessage) && (
            <Alert Error={(errors as any)?.general} Success={successMessage} />
          )}

          {activeTab === "signin" ? (
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-2">
                Welcome Back
              </h2>
              <p className="text-gray-600 mb-6 text-sm">
                Sign in to continue to your account
              </p>

              <SignInForm
                formData={SignInFormValue.formData}
                onsubmit={handleSignIn}
                UpdateField={SignInFormValue.updateField}
                loading={loading}
              />
            </div>
          ) : (
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-2">
                Create Account
              </h2>
              <p className="text-gray-600 mb-6 text-sm">
                Sign up to get started
              </p>
              <SignUpForm
                formData={SignUpFromValue.formData}
                onSubmit={handleSignUp}
                errors={errors}
                UpdateField={SignUpFromValue.updateField}
              />
            </div>
          )}

          <div className="relative my-6">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-300"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-4 bg-white text-gray-500">
                Or continue with
              </span>
            </div>
          </div>

          <button
            type="button"
            onClick={() => alert("Google Sign In clicked")}
            className="w-full flex items-center justify-center gap-3 px-4 py-2.5 border border-gray-300 rounded-lg hover:bg-gray-50 transition-all duration-300 shadow-sm hover:shadow-md"
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24">
              <path
                fill="#4285F4"
                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
              />
              <path
                fill="#34A853"
                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
              />
              <path
                fill="#FBBC05"
                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
              />
              <path
                fill="#EA4335"
                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
              />
            </svg>
            <span className="font-medium text-gray-700">
              Sign in with Google
            </span>
          </button>
        </div>
      </div>
    </div>
  );
}
